.progress {
  height: 60px;
  margin-bottom: 20px;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 2px rgba(0,0,0,0.1);
  box-shadow: inset 0 1px 2px rgba(0,0,0,0.1);
  }
  .progress {
  background-image: -webkit-gradient(linear,left 0,left 100%,from(#ebebeb),to(#f5f5f5));
  background-image: -webkit-linear-gradient(top,#ebebeb 0,#f5f5f5 100%);
  background-image: -moz-linear-gradient(top,#ebebeb 0,#f5f5f5 100%);
  background-image: linear-gradient(to bottom,#ebebeb 0,#f5f5f5 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffebebeb',endColorstr='#fff5f5f5',GradientType=0);
  }
  .progress {
  height: 20px;
  background-color: #ebeef1;
  background-image: none;
  box-shadow: none;
  }
  .progress-bar {
  float: left;
  width: 0;
  height: 100%;
  font-size: 20px;
  line-height: 40px;
  color: black;
  font-weight: bolder;
  text-align: center;
  background-color: #428bca;
  -webkit-box-shadow: inset 0 -1px 0 rgba(0,0,0,0.15);
  box-shadow: inset 0 -1px 0 rgba(0,0,0,0.15);
  -webkit-transition: width .6s ease;
  transition: width .6s ease;
  }
  .progress-bar {
  background-image: -webkit-gradient(linear,left 0,left 100%,from(#428bca),to(#3071a9));
  background-image: -webkit-linear-gradient(top,#428bca 0,#3071a9 100%);
  background-image: -moz-linear-gradient(top,#428bca 0,#3071a9 100%);
  background-image: linear-gradient(to bottom,#428bca 0,#3071a9 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff428bca',endColorstr='#ff3071a9',GradientType=0);
  }
  .progress-bar {
  box-shadow: none;
  border-radius: 3px;
  background-color: #0090D9;
  background-image: none;
  -webkit-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -moz-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -ms-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -o-transition: all 1000ms cubic-bezier(0.785, 0.135, 0.150, 0.860);
  transition: all 1000ms cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -webkit-transition-timing-function: cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -moz-transition-timing-function: cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -ms-transition-timing-function: cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -o-transition-timing-function: cubic-bezier(0.785, 0.135, 0.150, 0.860);
  transition-timing-function: cubic-bezier(0.785, 0.135, 0.150, 0.860);
  }
  .progress-bar-success {
  background-image: -webkit-gradient(linear,left 0,left 100%,from(#5cb85c),to(#449d44));
  background-image: -webkit-linear-gradient(top,#5cb85c 0,#449d44 100%);
  background-image: -moz-linear-gradient(top,#5cb85c 0,#449d44 100%);
  background-image: linear-gradient(to bottom,#5cb85c 0,#449d44 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff5cb85c',endColorstr='#ff449d44',GradientType=0);
  }
  .progress-bar-success {
  background-color: #0AA699;
  background-image: none;
  }
  .progress-bar-info {
  background-image: -webkit-gradient(linear,left 0,left 100%,from(#5bc0de),to(#31b0d5));
  background-image: -webkit-linear-gradient(top,#5bc0de 0,#31b0d5 100%);
  background-image: -moz-linear-gradient(top,#5bc0de 0,#31b0d5 100%);
  background-image: linear-gradient(to bottom,#5bc0de 0,#31b0d5 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff5bc0de',endColorstr='#ff31b0d5',GradientType=0);
  }
  .progress-bar-info {
  background-color: #0090D9;
  background-image: none;
  }
  .progress-bar-warning {
  background-image: -webkit-gradient(linear,left 0,left 100%,from(#f0ad4e),to(#ec971f));
  background-image: -webkit-linear-gradient(top,#f0ad4e 0,#ec971f 100%);
  background-image: -moz-linear-gradient(top,#f0ad4e 0,#ec971f 100%);
  background-image: linear-gradient(to bottom,#f0ad4e 0,#ec971f 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fff0ad4e',endColorstr='#ffec971f',GradientType=0);
  }
  .progress-bar-warning {
  background-color: #FDD01C;
  background-image: none;
  }
  .progress-bar-danger {
  background-image: -webkit-gradient(linear,left 0,left 100%,from(#4fd961),to(#2cc9c9));
  background-image: -webkit-linear-gradient(top,#4fd971 0,#46c92c 100%);
  background-image: -moz-linear-gradient(top,#5ad94f 0,#2cc96d 100%);
  background-image: linear-gradient(to bottom,#4fd97d 0,#31c92c 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffd9534f',endColorstr='#ffc9302c',GradientType=0);
  }
  .progress-bar-danger {
  background-color: rgb(119, 175, 144);
  background-image: none;
  }